import React, { Component } from 'react';
import {
  VerticalGridLines,
  VerticalBarSeries,
  LabelSeries, XYPlot, XAxis, YAxis, LineSeries, HorizontalGridLines
} from 'react-vis';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import TEXT from './db.txt'
class Stock extends Component {
  constructor(props) {
    super(props);
    fetch(TEXT)
      .then(response => response.text()
        .then(text => {
          let Symbols = text.split('\n');
          let Symbol = JSON.parse(Symbols[this.props.stock]);
          console.log(Symbol);
          this.setState({ Symbol })

        }));

  }

  render() {
    if (this.state === null) return <div></div>;
    let S = this.state.Symbol;
    let news = "";
    let earnings = "";
    let PT = "";
    let HL = "";
    if (S.news !== null && S.news !== undefined && S.news.length !== 0) {
      news = S.news.map(nw => (
        <div>
          <span>{new Date(nw.datetime * 1000).toLocaleDateString('en-US')}</span>
          <a href={nw.url}>{nw.headline}</a>
        </div>
      ))
      news = <div><h4>Company News</h4>{news}</div>
    }
    if (S.earnings !== null && S.earnings !== undefined && S.earnings.length !== 0) {
      let count = 0;
      let data = [];
      let x = S.earnings.length;
      earnings = S.earnings.map(earning => {
        data.push({ x: x--, y: earning.actual });
        if (earning.actual > 0) count++;
        return (<div className="pb-3">
          <div>Actual Earnings:{earning.actual}</div>
          <div>Estimate Earnings:{earning.estimate.toFixed(4)}</div>
          <span>Period: {earning.period}</span>
        </div>
        )
      })
      earnings =
        <div>
          <h4>Company Earnings</h4>
          {earnings}
          {(data.length > 0) ?
            <XYPlot
              width={300}
              height={300}>
              <HorizontalGridLines />
              <LineSeries
                colorType="literal"
                color="red"
                data={data} />
              <XAxis title="X" />
              <YAxis />
            </XYPlot> : <div></div>}
          <div>This company made money in {count} of the last 4 periods</div>
        </div>
    }
    if (S.PT !== null && S.PT !== undefined && S.PT.targetHigh !== 0) {
      let bardata = [{ x: "High", y: S.PT.targetHigh }, { x: "Mean", y: S.PT.targetMean }, { x: "Low", y: S.PT.targetLow }]
      PT =
        <div className="pb-3">
          <div>High:{S.PT.targetHigh}</div>
          <div>Low:{S.PT.targetLow}</div>
          <div>Mean:{S.PT.targetMean}</div>
          <div>Median:{S.PT.targetMedian}</div>
        </div>

      PT =
        <div>
          <h4>Price Target</h4>
          {PT}

          <div>
            <XYPlot xType="ordinal" width={300} height={300} xDistance={100}>
              <VerticalGridLines />
              <HorizontalGridLines />
              <XAxis />
              <YAxis />
              <VerticalBarSeries className="vertical-bar-series-example" data={bardata} />
            </XYPlot>
          </div>

          <div>{(S.PT.targetLow * 2 > S.PT.targetHigh) ?
            "There is an agreement on the upcoming value of the stock" :
            "There is not an agreement on the upcoming value of the stock"}
          </div>
        </div>
    }
    if (S.HL !== null && S.HL !== undefined && S.HL.symbol !== "") {
      let hlbardata = [{ x: "Low compared to high", y: S.HL.metric['52WeekLow'] }]
      let hlbardatab = [{ x: "Low compared to high", y: (S.HL.metric['52WeekHigh'] - S.HL.metric['52WeekLow']) }]
      HL =
        <div className="pb-3">
          <div>High:{S.HL.metric['52WeekHigh']}</div>
          <div>High Date:{S.HL.metric['52WeekHighDate']}</div>
          <div>Low:{S.HL.metric['52WeekLow']}</div>
          <div>Low Date:{S.HL.metric['52WeekLowDate']}</div>
        </div>
      HL =
        <div>
          <h4>52 Week High/Low</h4>
          {HL}
          <XYPlot xType="ordinal" width={300} height={300} stackBy="y">
            <VerticalGridLines />
            <HorizontalGridLines />
            <XAxis />
            <YAxis />
            <VerticalBarSeries className="vertical-bar-series-example" data={hlbardata} />
            <VerticalBarSeries data={hlbardatab} />
          </XYPlot>
          <div>{(S.HL.metric['52WeekLow'] * 2 > S.HL.metric['52WeekHigh']) ?
            "This stock has a low disparity" :
            "This stock has a high disparity"}
          </div>
        </div>
    }
    return (
      <div className="App" key="app" >

        <div className="d-flex pb-3 pt-2 vw-100 justify-content-center">
          <div className="card " style={{ "width": "75vw" }}>
            <div className="card-body">
              <h5 className="card-title">{S.description}</h5>
              <div>{S.symbol}</div>
              <div className="container">
                <div className="row">
                  <div className="d-flex text-left col-12 col-xl-6" style={{ "overflow-wrap": "break-word" }}>{news}</div>
                  <div className="d-flex text-left col-12 col-xl-3 ">{earnings}</div>
                  <div className="col-12 col-xl-3">
                    <div className="d-flex text-left pl-5">{PT}</div>
                    <div className="d-flex text-left pl-5">{HL}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );

  }
}

class App extends Component {
  constructor() {
    super();
    this.state = { stock: 0, reload: false, time: false }
  }
  setStock(stock) {
    if (stock < 0) {
      this.setState({ time: false })
      return;
    }
    this.setState({ stock, time: false })
  }
  render() {

    return (
      <div key="app" >
        <div className="d-flex justify-content-center vw-100">
          <button className="btn" onClick={() => { if (!this.state.time) this.setState({ time: setTimeout(() => { this.setStock(this.state.stock - 1) }, 100) }) }}>Previous Stock</button>
          <button className="btn" onClick={() => { if (!this.state.time) this.setState({ time: setTimeout(() => { this.setStock(this.state.stock + 1) }, 100) }) }}>Next Stock</button>
        </div>
        <Stock stock={this.state.stock} key={this.state.stock}> </Stock>
      </div>
    )
  }
}

export default App;
